import {
  LaunchStartedEvent,
  InitialRenderFinishedEvent,
  SegmentAnalytics,
  Analytics,
} from "./analytics";

interface Measures {
  launchStarted: LaunchStartedEvent | undefined;
  initialRender: InitialRenderFinishedEvent | undefined;
}

export default class ApplicationListener implements IApplicationListener {
  private static instance: ApplicationListener | undefined;

  static factory(): ApplicationListener {
    if (ApplicationListener.instance === undefined) {
      ApplicationListener.instance = new ApplicationListener(
        new SegmentAnalytics(window),
      );
    }

    return ApplicationListener.instance;
  }

  measures: Measures = {
    launchStarted: undefined,
    initialRender: undefined,
  };

  constructor(private analytics: Analytics) {
    if (!analytics) throw new Error("Missing analytics");
  }

  applicationLaunchStarted() {
    this.measures.launchStarted = new LaunchStartedEvent();
  }

  applicationDidFinishInitialRender() {
    this.measures.initialRender = new InitialRenderFinishedEvent();
  }

  applicationDidInitializeAnalytics() {
    this.analytics.sendTiming(this.measures.initialRender);
    this.analytics.sendTiming(this.measures.launchStarted);
    this.measures.initialRender = undefined;
    this.measures.launchStarted = undefined;
  }
}

interface IApplicationListener {
  applicationLaunchStarted(): void;
  applicationDidFinishInitialRender(): void;
  applicationDidInitializeAnalytics(): void;
}
