/**
 * Implement Gatsby's Browser APIs in this file.
 *
 * See: https://www.gatsbyjs.org/docs/browser-apis/
 */
const Cookies = require("js-cookie");
const { globalHistory } = require("@reach/router");
const { rejectCookiesItems } = require("./src/utils/analytics");
const ApplicationListener = require("./src/telemetry/ApplicationListener")
  .default;

exports.onClientEntry = async () => {
  const appListener = ApplicationListener.factory();
  appListener.applicationLaunchStarted();

  if (!localStorage.ccpa) {
    let domain =
      window.location.hostname !== "localhost" ? ".brex.com" : "localhost";
    if (window.location.href.indexOf("netlify") > -1) {
      domain = window.location.hostname;
    }

    const response = await fetch(
      "https://api.ipdata.co/?api-key=c3f3277c5fa51b3e681374d5ce5cf5db0efc7466166922ee42074acc",
      {
        method: "GET",
      },
    );
    const result = await response.json();

    if (result.region === "California" && result.region_code === "CA") {
      const cookieValues = {
        version: 2,
        destinations: rejectCookiesItems,
      };

      Cookies.set("tracking-preferences", cookieValues, {
        expires: 365,
        domain,
      });
    } else {
      Object.keys(rejectCookiesItems).forEach(
        /* eslint-disable no-return-assign */
        (cookie) => (rejectCookiesItems[cookie] = true),
      );
      const cookieValues = {
        version: 1,
        destinations: rejectCookiesItems,
      };
      Cookies.set("tracking-preferences", cookieValues, {
        expires: 365,
        domain,
      });
    }
  } else {
    // manually loading analytic when ccpa banner approved or reject, to continue tracking process
    window.analytics.load(process.env.SEGMENT_KEY);
    appListener.applicationDidInitializeAnalytics();
  }
};

exports.onInitialClientRender = () => {
  const appListener = ApplicationListener.factory();
  appListener.applicationDidFinishInitialRender();

  /**
   * This is a workaround for a bug in Gatsby
   *
   * See https://github.com/gatsbyjs/gatsby/issues/8357 for more details
   */
  globalHistory._onTransitionComplete();
  if (!localStorage.ccpa) {
    let domain =
      window.location.hostname !== "localhost" ? ".brex.com" : "localhost";
    if (window.location.href.indexOf("netlify") > -1) {
      domain = window.location.hostname;
    }

    const trackingPreferences = JSON.parse(
      Cookies.get("tracking-preferences", {
        domain,
      }),
    );

    if (
      trackingPreferences !== undefined &&
      Number(trackingPreferences.version) === 1
    ) {
      Object.keys(rejectCookiesItems).forEach(
        /* eslint-disable no-return-assign */
        (cookie) => (rejectCookiesItems[cookie] = true),
      );
      localStorage.ccpa = "accepted";
    }
    // this is need to have additional pipe to load analytic and not blocking page speed for google, this code will runs for sure, doesn't matter if user will moves to other page on the site
    setTimeout(async () => {
      await window.analytics.load(process.env.SEGMENT_KEY, rejectCookiesItems);
      appListener.applicationDidInitializeAnalytics();
      window.analytics.track("Cookie Compliance Checked", {
        target: "noBannerRequired",
      });
      window.analytics.track("ccpa-approved");
    }, 5000);
  }
};

exports.onRouteUpdate = () => {
  // the trackPage option in the plugin actually only triggers a page call on
  // the initial browser page load, not for js route navigations thereafter.
  // https://github.com/benjaminhoffman/gatsby-plugin-segment-js/issues/6
  // This is gatsby native hook for js route navigation, so we call segment's
  // page tracking function here.
  if (window.analytics) {
    window.analytics.page();
  }
};
