enum TimingName {
  NAV_START_TO_APP_LAUNCH_START = "NAV_START_TO_APP_LAUNCH_START",
  NAV_START_TO_INITIAL_RENDER_COMPLETION = "NAV_START_TO_INITIAL_RENDER_COMPLETION",
}

enum TimingCategory {
  LIFECYCLE = "LIFECYCLE",
  LOADING = "LOADING",
}

export class LaunchStartedEvent {
  readonly category = TimingCategory.LIFECYCLE;
  readonly name = TimingName.NAV_START_TO_APP_LAUNCH_START;
  readonly duration = getElapsedTimeFromNavStart();
}

export class InitialRenderFinishedEvent {
  readonly category = TimingCategory.LIFECYCLE;
  readonly name = TimingName.NAV_START_TO_INITIAL_RENDER_COMPLETION;
  readonly duration = getElapsedTimeFromNavStart();
}

type TimingEvent = LaunchStartedEvent | InitialRenderFinishedEvent;

export interface Analytics {
  sendTiming(event: TimingEvent | undefined): void;
}

type WindowWithSegmentAnalytics = Window & {
  analytics: SegmentAnalytics.AnalyticsJS | undefined;
};

export class SegmentAnalytics implements Analytics {
  constructor(private window: WindowWithSegmentAnalytics) {
    if (!window) throw new Error("Missing window");
  }

  sendTiming(event: TimingEvent | undefined): void {
    if (!event) return;
    if (!this.window.analytics) return;
    this.window.analytics.ready(() => {
      if (!window.ga) return;
      window.ga("send", {
        hitType: "timing",
        timingCategory: event.category,
        timingVar: event.name,
        timingValue: event.duration,
      });
    });
  }
}

export class ConsoleAnalytics implements Analytics {
  sendTiming(event: TimingEvent | undefined): void {
    if (!event) return;
    console.log(event);
  }
}

export class CompositeAnalytics implements Analytics {
  analytics: Analytics[];
  constructor(...analytics: Analytics[]) {
    this.analytics = analytics.filter((a) => a);
  }

  sendTiming(event: TimingEvent | undefined): void {
    this.analytics.forEach((analytics) => analytics.sendTiming(event));
  }
}

function getElapsedTimeFromNavStart() {
  return Math.round(performance.now());
}
